<template>
<div class="login_main">
  <div class="content">
    <div class="panel">
      <div class="group">
        <ul>
          <li><label>用户名</label></li>
          <li><el-input type="text" :clearable='true' prefix-icon="el-icon-user" v-model="obj.name" placeholder=" 请输入用户名" /></li>
        </ul>
        <ul>
          <li><label>密码</label></li>
          <li><el-input type="password"  :clearable='true' prefix-icon='el-icon-warning' v-model="obj.pwd" placeholder=" 请输入密码" /></li>
        </ul>
      </div>
      <div class="login">
        <el-button type="primary" @click="btnClick">登录</el-button>
      </div>
      <div class="warning">
        <h5 v-if="passwordError">您输入的密码有误，请重新输入！</h5>
        <h5 v-if="userError">您输入的用户不存在，请重新输入或注册用户！</h5>
      </div>
       <div class="register">
      <router-link to="/register" tag="button">注册</router-link>
      <router-link to="/modifypassword" tag="button">修改密码</router-link>
    </div>
    </div>

  </div>
</div>
</template>
<script>
  import {request} from "../../network/request";
  import nkLogin from './nkLogin'
  export default {
    name: "Login",
    data(){
      return{
        userError:false,
        passwordError:false,
        obj:{
          name:"",
          pwd:""
        }
      }
    },
    components:{nkLogin},
    methods:{
      btnClick(){
        this.userError=false;
        this.passwordError=false;
        const jon=JSON.stringify(this.obj);
        request({
          url: '/login',
          method:'post',
          data:jon,
          headers: {'Content-Type': 'application/json;charset=UTF-8'}
        }).then(res=>{
          console.log(res);
          localStorage.setItem("token", res.data.token);
          console.log(localStorage.getItem("token"));
          this.$store.commit('addToken',res.data.token);
          this.$store.commit('addName',res.data.name);
          this.$store.commit('changeLogin');
          this.$router.push('/index');
        }).catch(err=>{
          console.log(err.response);
          console.log(typeof err.response.data.error);
          switch(err.response.data.error){
            case true:
              this.passwordError=true;
              break;
            case false:
              this.userError=true;
              break;
          }
        });
      }
    }
  }

</script>

<style scoped>
.login_main{
  min-width: 1280px;
}
  .content{
    /*border:1px solid red;*/
    width:300px;
    margin: 0 120px;
     margin:0 auto;

  }

  .content .panel{
      margin:130px 0;
    border:1px solid #ccc;
    border-radius:5px;
    /* height:220px; */
    background-color:#fff;
  }
  .content .panel .group{
    margin:30px 10px 10px 20px;
  }
  .content .panel .group ul{
    list-style: none;
    margin-top:15px;
  }

  .content .panel .group ul li{
    margin-top:2px;
  }
  .warning{
    display: flex;
    justify-content: center;
    color: red;
  }

  .content .panel .group ul li input{
    line-height:22px;
    width:250px;
    font-size: 14px;
    font-family: "微软雅黑","宋体";
  }
  .content .panel .group ul li label{
    font-size: 14px;
    font-family: "黑体","微软雅黑","宋体";
    font-weight: bold;
  }

  .content .panel .login{
    text-align: center;
    /*border:1px solid red;*/
    margin-top:20px;

  }
  .login{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin:10px 0 ;
  }
  .el-button{
    width: 80%;
  }
  /* .content button{
    width:255px;
    height:30px;
    background-color:#008000;
    border:none;
    color:#fff;
    font-size:16px;
    font-weight: bold;
    cursor:pointer;
  } */


  .content .register{
    margin-top:15px;
    display: flex;
    justify-content: center;
  }

  .content .register button{
    width:80px;
    height:30px;
    border:none;
    font-size:12px;
    background-color:#409EFF;
    border: none;
    border-radius:3px;
    color:#fff;
    font-family: "宋体";
    text-align: center;
    /* margin-left:30px; */
    margin:0 10px 20px 10px;

  }
   .content .register button:first-child{
     background: coral;
   }
</style>